import { useDispatch, useSelector } from 'react-redux'
import { addItem } from '../actions/cartActions'

export default function Products() {
    const items = useSelector(state=>state.cart.items)
    const dispatch = useDispatch()
    return (
        <div className="wrapper">
            <div className="title">
                <span>Beautiful Brushes</span>
            </div>
            <div className="products">
                {items.map((item,i)=>
                <div className="card" key={i}>
                    <div className="card-image">
                        <img src={item.images[0]} alt={item.productID}/>
                        <span className="card-title">{item.productID} ~ ${item.price}</span>
                    </div>
                    <div className="card-content">
                        <p><input type='button' value='Add to Cart' onClick={()=> dispatch(addItem(item.productID))} className='btn btn-outline-success'/></p>
                    </div>
                </div> 
                )}
            </div>
        </div>
    )
}