import { BASE_URL } from './baseURL'

export const CREATE = 'CREATE';
export const REVIEWS = 'REVIEWS';

export const create = (params)=>{
    return fetch(`${BASE_URL}/review/create_review`, {
        headers: { 'Content-Type':'application/json' },
        credentials: 'include', 
        method: 'POST',
        body: JSON.stringify(params)
    }).then(res=>res.json())
    .then(data=> data)
}
export const reviews = ()=>{
    return fetch(`${BASE_URL}/review/reviews`, {
        headers: { 'Content-Type':'application/json' },
        credentials: 'include', 
        method: 'GET'
    }).then(res=>res.json())
    .then(data=> data)
}


  